import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { UtilityComponent } from './utility.component';
import { UtilityReportsComponent } from './components/utility-reports/utility-reports.component';
import { UtilitySetupComponent } from './components/utility-setup/utility-setup.component';

const routes: Routes = [
	
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: UtilitySetupComponent},
	{ path: 'reports', component: UtilityReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UtilityRoutingModule { }
